// -----------------------------------------------------------------------------
// This file contains styles that are specific to the home page.
// -----------------------------------------------------------------------------

body {
  background: url(/src/img/background.jfif) center center / cover;
  transform: translateX(0%);
}

.main {
  display: flex;
}

.content {
  width: 100%;
  height: 100%;
}

.dashboard {
  height: 80rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
  @include flex-placeholder(center, flex-start, row);
  @include respond(tab-port) {
    @include flex-placeholder(center, center, column);
  }
}

.dashboard__users {
  overflow-y: scroll;
  /* width: 35rem; */
  height: 80rem;
  // background-color: rgba(255, 255, 255, 0.2);
  margin-left: 1rem;
  border-radius: 1rem;
  margin-bottom: 2rem;
  text-align: center;
  width: 145rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;

  @include respond(tab-land) {
    width: 98%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    height: 100%;
    margin-left: unset;
  }
  @include respond(tab-port) {
    width: 95%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 100%;
    margin-left: unset;
  }

  @include respond(phone) {
    grid-template-columns: 1fr;
  }

  @include respond(low-phone) {
    // @include flex-placeholder(center, center, column);
  }
}

.dashboard__updates {
  width: 100%;
}
